.cta {
  padding-top: 115px;
  padding-bottom: 80px;

  &__heading {
    margin-bottom: 60px;
  }

  &__title {
    position: relative;
    margin-bottom: 30px;

    &::before {
      position: absolute;
      content: "КОНСУЛЬТАЦИЯ";
      left: 50%;
      top: -35px;
      transform: translateX(-50%);
      font-family: 'Igra Sans', sans-serif;
      font-size: 68px;
      line-height: 103%;
      text-transform: uppercase;
      background: linear-gradient(180deg, #525556 0%, rgba(82, 85, 86, 0) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      opacity: 0.1;
    }
  }

  &__descr {
    span {
      font-weight: 600;
      color: var(--accent-color);
    }
  }

  .form {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
    width: 100%;

    &__label {
      position: relative;
      width: 100%;

      &--name::before {
        position: absolute;
        content: "";
        top: 25px;
        right: 25px;
        width: 30px;
        height: 30px;
        background-image: url('../img/form-name.svg');
        background-repeat: no-repeat;
      }

      &--phone::before {
        position: absolute;
        content: "";
        top: 25px;
        right: 25px;
        width: 30px;
        height: 30px;
        background-image: url('../img/form-phone.svg');
        background-repeat: no-repeat;
      }
    }

    &__input {
      position: relative;
      padding: 31px 60px 31px 30px;
      font-weight: 500;
      font-size: 16px;
      line-height: 18px;
      color: var(--primary-color);
      width: 100%;
      height: 80px;
      background-color: rgba($color: #eeeeee, $alpha: 0.5);

      &::placeholder {
        font-weight: 300;
      }
    }

    &__btn {
      width: 100%;
    }
  }


}
